<template>
  <v-data-table
    :headers="headers"
    :items="monthlyArray"
    item-key="OriginalId"
    dense
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Monthly Plan</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
              Update
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="text-h5">Update</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-form v-model="valid">
                  <v-row v-for="(item, i) in dmonthlyArray" :key="i">
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        disabled
                        :value="item.id"
                        label="Portfolio ID"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        v-model="item.value"
                        :rules="[rules.required, rules.min]"
                        label="Value"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-form>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close"> Cancel </v-btn>
              <v-btn color="blue darken-1" text :disabled="!valid" @click="save"> Save </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
  </v-data-table>
</template>

<script>
import axios from "../../api/index";
export default {
  props: {
    monthlyObject: Object,
  },
  data: () => ({
    valid: false,
    dialog: false,
    headers: [
      {
        text: "ID",
        align: "start",
        sortable: false,
        value: "id",
      },
      { text: "Value", value: "value" },
    ],
    monthlyArray: [],
    dmonthlyArray: [],
    rules: {
      required: (value) => !!value || "Required.",
      min: (value) => value >= 0 || "Must more than 0",
    },
  }),
  watch: {
    dialog(val) {
      if (val) {
        this.dmonthlyArray = JSON.parse(JSON.stringify(this.monthlyArray));
      }
      val || this.close();
    },
    monthlyObject() {
      let obj = this.monthlyObject.portfolio;
      for (let o in obj) {
        obj[o]["OriginalId"] = o;
        this.monthlyArray.push(obj[o]);
      }
    },
  },

  methods: {
    close() {
      this.dialog = false;
    },

    save() {
      let obj = {};
      this.dmonthlyArray.forEach((el) => {
        obj[el.OriginalId] = el;
        delete el["OriginalId"];
      });
      axios
        .put(`/api/v1/depositplan/${this.monthlyObject.id}`, { portfolio: obj })
        .then(() => {
          this.monthlyArray = JSON.parse(JSON.stringify(this.dmonthlyArray));
        });

      this.close();
    },
  },
};
</script>