<template>
  <v-container fill-height>
    <v-row class="text-center" align="center" justify="center">
      <v-col cols="6">
        <OneTime :oneTimeObject="oneTimeObject"></OneTime>
      </v-col>
      <v-col cols="6">
        <Monthly :monthlyObject="monthlyObject"></Monthly>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from "../../api/index";
import OneTime from "./OneTime.vue";
import Monthly from "./Monthly.vue";
export default {
  name: "Login",
  components: {
    OneTime,
    Monthly,
  },
  created() {
    axios
      .get("/api/v1/depositplan")
      .then((res) => {
        this.monthlyObject = res.data.product.find(
          (el) => el.isMonthly === true
        );
        this.oneTimeObject = res.data.product.find(
          (el) => el.isMonthly === false
        );
      })
      .catch((error) => {
        console.log(error);
      });
  },
  data: () => ({
    monthlyObject: {},
    oneTimeObject: {},
    monthlyPortfolio: [],
    switch1: true,
    valid: true,
    name: "",
    nameRules: [
      (v) => !!v || "Name is required",
      (v) => (v && v.length <= 10) || "Name must be less than 10 characters",
    ],
    referenceNo: "",
    emailRules: [(v) => !!v || "E-mail is required"],
    select: null,
    items: ["Item 1", "Item 2", "Item 3", "Item 4"],
    checkbox: false,
    value: "",
    rules: {
      required: (value) => !!value || "Required.",
      min: (v) => v.length >= 8 || "Min 8 characters",
    },
  }),
  methods: {
    validate() {
      this.$refs.form.validate();
    },
    reset() {
      this.$refs.form.reset();
    },
    send() {
      axios
        .post("/api/v1/transaction", {
          ref: this.referenceNo,
          value: this.value,
          isMonthly: this.switch1,
        })
        .then((user) => {
          this.$router.push({ path: "/" });
          console.log(user);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
